.form-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;

    .concert-input-form {
        font-family: "Spline Sans Mono", monospace;
        text-transform: uppercase;

        border: 1px solid black;
        border-radius: 8px;
        box-sizing: border-box;
        display: flex;
        justify-content: start;
        flex-direction: column;
        gap: 12px;
        padding: 12px;
    }

    .button-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 12px;
        padding-top: 10px;
    
        button {
            width: 100%;
        }
    }
}